<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>创建用户</span>
            <el-button type="primary" round>返回</el-button>
        </div>

        <el-form label-width="80px">
            <el-form-item label="角色名称">
                <el-input placeholder="请输入内容" v-model="form.role_name"></el-input>
            </el-form-item>
            <el-form-item label="角色描述">
                <el-input placeholder="请输入内容" v-model="form.role_describe"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">提交</el-button>
                <el-button>重置</el-button>
            </el-form-item>
        </el-form>
    </el-card>
</template>
<script>
import {createRoleApi} from '@/api/roles.js'
export default {
    data() {
        return {
            form: {
                role_name: '',
                role_describe: ''
            }
        }
    },
    methods: {
        onSubmit() {
            createRoleApi({
                role_name: this.form.role_name,
                role_describe: this.form.role_describe
            })
            .then(res => {
                // console.log(res);
                if(res.meta.state == 201) {
                    this.$message.success(res.meta.msg)
                }else {
                    this.$message.error(res.meta.msg)
                }
            })
        }
    }
}
</script>
<style scoped>
    .el-card .clearfix {
        display: flex;
        justify-content: space-between;
    }
</style>